/**
 * 
 * @author  
 * @date    2019/11/16 18:49
 * @param   func                //
 * @param   wait                //func
 * @param   options             //
 * @param   options.trailing    //true
 * @param   options.leading     //
 */
export const throttle = <T extends Function>(func: T, wait: number, options: { trailing?: boolean, leading?: boolean } = {}): T => {
    let args: any, context: any, previous = 0, timeout: any;
    let later = () => {
        // console.log('later click' + Date.now() % 10000)
        previous = options.leading === false ? 0 : Date.now()
        func.apply(context, args)
        args = context = timeout = null
    }

    return function () {
        args = arguments
        // @ts-ignore
        context = this
        let now = Date.now()

        if (!previous && options.leading === false) {
            previous = now
        }

        let remaining = wait - (now - previous)

        if (remaining <= 0) {
            /**/
            if (timeout) {
                clearTimeout(timeout)
                timeout = null
            }
            // console.log('throttle click' + Date.now() % 10000)
            func.apply(context, args)
            previous = now

        } else if (!timeout && options.trailing !== false) {
            timeout = setTimeout(later, remaining)
        }
    } as any
}