export const insertSort = <T>(arr: T[], func: (prev: T, next: T) => boolean): T[] => {
    let len = arr.length;
    for (let i = 1; i < len; i++) {
        let temp = arr[i];
        /**/
        let j = i - 1;
        /**/
        while (j >= 0 && func(arr[j], temp)) {
            /**/
            arr[j + 1] = arr[j];
            j--;
        }
        arr[j + 1] = temp;
    }
    return arr
}